@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap");


.icon{
        margin-bottom: 0;
        padding-top: 1em;
        font-size: 16px;
        width: 25px;
        height: 25px;
    
}
body {
  margin: 0px;
  font-family: "Raleway", sans-serif;
  background-color: #f2f6ff;
  height: 1000px;
}
.main {
  background-color: #e7f1f9;
}

.body {
  width: 70%;
  margin-right: auto;
  margin-left: auto;
  margin-top: 100px;
}

.main-body {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.header {
  position: fixed;
  top: 0px;
  width: 90%;
  padding: 7px 78px;
  background-color: #fff;
  box-shadow: 4px 4px 8px #cccccc;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Raleway", sans-serif;
}
.left {
  width: 27%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
#logo {
  width: 130px;
  font-size: 32px;
  font-weight: bolder;
  font-family: "Raleway", sans-serif;
  text-decoration: none;
  color: #0084ff;
  font-weight: 900;
}
#logo-stories-text {
  margin: 0px;
  font-weight: 600;
  font-family: "Raleway", sans-serif;
  font-style: Italic;
  margin-top: 0px;
  letter-spacing: 1px;
}
#dropdown-text {
  color: #000000;
  padding-left: 50px;
  font-family: "Raleway", sans-serif;
  cursor: pointer;
  font-weight: 500;
}
.fa-chevron-down {
  color: #5f5f5f;
}
.right {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 70px;
  /* letter-spacing: 0.4px; */
}
.header-para {
  color: #7b7b7b;
  opacity: 0.7;
}
.btn {
  padding: 13px;
  width: 250px;
  background-color: #0084ff;
  border: none;
  border-radius: 40px;
  color: white;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
}
.fa-filter {
  color: #0096ff;
}
#filter-text {
  font-size: 14px;
  font-weight: 600;
  margin-left: 8px;
  font-family: "Raleway", sans-serif;
}
.button {
  padding: 6px 14px;
  width: auto;
  color: #0084ff;
  border-radius: 15px;
  border: 1px solid #0084ff;
  margin: 5px;
  cursor: pointer;
  background-color: transparent;
}
.button:hover {
  background-color: #0084ff;
  color: white;
}

.active-category {
  color: #fff;
  background-color: #0084ff;
}

.card {
  width: 31%;
  background-color: #fff;
  display: inline-block;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  margin-right: 20px;
  box-shadow: 4px 4px 8px #cccccc;
  margin-top: 20px;
  cursor: pointer;
  border: 0.1px solid #62beff;
  border-radius: 5px;
}

.image-section {
  height: 176px;
}

.image {
  width: 100%;
  height: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.text-section {
  padding: 10px 24px;
}

#title {
  margin: 0px 0px 10px 0px;
  font-weight: 600;
  text-align: left;
  font-family: "Raleway", sans-serif;
  height: 20px;
}

#edyoda-text {
  color: #0096ff;
  font-size: 14px;
  font-weight: 600;
  font-family: "Raleway", sans-serif;
}

#date {
  color: rgba(0, 0, 0, 0.7);
  font-size: 14px;
  font-weight: 300;
  font-family: "Roboto", sans-serif;
}
.para-div {
  height: 110px;
  margin-top: -20px;
}

.card-para {
  color: #b3b3b3;
}
footer {
  margin-top: 50px;
  background-color: #172146;
  padding: 40px 16%;
}
